import { Component, OnInit } from '@angular/core';
import { ValidationErrors } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
//
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
//
import { CanDeactivateMixin } from '../../../../../ui/helpers/component-can-deactivate';
import { ConfirmDialogComponent, } from '../../../../../ui/modules/confirm-dialog/components/confirm-dialog/confirm-dialog.component';
import { Mixin } from '../../../../../ui/helpers/mixin-decorator';
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
import { ToastrService } from '../../../../../error-handling/services/toastr.service';
import { Brand } from '../../models/brand';
import { BrandsService } from '../../services/brands.service';
import { BaseRouteComponent } from '../../../../../routing/components/base-route-component';
import { NewBrandComponent } from '../new-brand/new-brand.component';

const errorKey = 'Error';

const savedStyleMessageKey = 'Saved Style Message';

@Component({
  selector: 'brand-creator',
  templateUrl: './brand-creator.component.html',
  styleUrls: ['./brand-creator.component.scss']
})
@Mixin([CanDeactivateMixin])
export class BrandCreatorComponent implements CanDeactivateMixin, OnInit {

  data: Brand = {
    name: "",
    description: ""
  };

  brands: Array<Brand>;

  // Begin Mixin code of the CanDeactivate class
  unsavedChanges = false;

  cancelBtnKey = 'No';

  okBtnKey = 'Yes';

  saveTitleKey = 'Discard Title';

  saveMessageKey = 'Discard Message';

  //modalRef: MatDialogRef<ConfirmDialogComponent>;

  modalRef: MatDialogRef<NewBrandComponent>;

  canDeactivate: () => Observable<boolean> | boolean;

  dataChanged: () => void;


  validationErrors: ValidationErrors;

  constructor(
    public activatedRoute: ActivatedRoute,
    public brandsService: BrandsService,
    private errorHandlingService: ErrorHandlingService,
    public router: Router,
    private translate: TranslateService,
    private toastr: ToastrService,
    public dialog: MatDialog) {
  }

  ngOnInit() {
    this.brands = this.activatedRoute.snapshot.data.brands;
  }

  submit(data: Brand) {
    this.createUser(data);
  }

  cancel() {
    this.close();
  }

  close() {
    this.router.navigate(this.activatedRoute.snapshot.data.closeRouteCommand, { relativeTo: this.activatedRoute });
  }

  createUser(data: Brand) {

    this.brandsService.postBrand(data).subscribe(response => {
      this.unsavedChanges = false;
      //this.close.emit();TODO
      this.close();
      this.toastr.success(savedStyleMessageKey);
    },
      (error: HandledError) => {
        this.errorHandlingService.handleUiError(errorKey, error);
        this.validationErrors = error.formErrors;
      });
  }

  showModal() {
    this.modalRef = this.dialog.open(NewBrandComponent, {
      height: '800px',
      width: '60%',
      data: {data: this.data}
    });
}

}
